
import axios from "axios";
export default {
  name: "IndexPage",
  head() {
    return { title: `${this.doctor.name} - ${this.$pageTitle}` };
  },
  data() {
    return {
      doctor: {},
    };
  },

  async asyncData({ route, store, error }) {
    try {
      const { slug } = route.params;
      let res = await axios.get(`${store.getters.baseUrl}/api/fetch/doctor`, {
        params: { slug },
      });

      if (res.data) {
        const { doctor } = res.data;
        return { doctor };
      } else {
        error({ statusCode: 404, message: "Not found" });
      }
    } catch (err) {
      error({
        statusCode: 500,
        message: "Something wrong. Please try again",
      });
    }
  },
};
